@use 'base/variables' as v;
@use 'base/mixins' as m;

.hero {
    background-image: linear-gradient(rgba(0, 0, 0, 0.6), rgba(0, 0, 0, 0.6)), image-set(url('/build/img/code.png') 1x, url('/build/img/code.webp') 1x);
    background-repeat: no-repeat;
    background-size: cover;
    position: relative;

    min-height: 100vh;

    .contenido-hero {
        color: v.$blanco;
        position: absolute;
        width: 100%;
        height: 100%;

        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;

        @include m.tablet {
            flex-direction: row;
            gap: 5rem;
        }

        img {
            width: 20rem;
            height: 28rem;

            @include m.tablet {
                width: 25rem;
                height: 35rem;
            }

            @include m.escritorio {
                width: 35rem;
                height: 45rem;
            }
        }

        h1 {
            font-size: 3rem;
            font-weight: 700;
            font-family: v.$fuenteTitulos;
            text-align: center;
            color: v.$azul;
            letter-spacing: 1rem;
            padding: 1rem;
            margin-top: 1rem;

            @include m.tablet {
                font-size: 3.5rem;
            }

            @include m.escritorio {
                font-size: 4rem;
            }
        }

        p {
            text-align: center;
            color: v.$blanco;
            font-size: 2rem;

            @include m.tablet {
                font-size: 2rem;
            }

            @include m.escritorio {
                font-size: 2.8rem;
            }
        }

        a {
            background-color: v.$azul;
            border: none;
        }
    }
}