@use 'base/variables' as v;
@use 'base/mixins' as m;

.primeraSeccion {
    text-align: center;

    @include m.tablet {
        @include m.grid(2, 3rem);
        text-align: justify;
    }

    .sobreMi {
        a {
            background-color: v.$azul;
            border: none;
        }

        p {
            font-size: 2rem;
            line-height: 1.5;

            @include m.tablet {
                font-size: 2.5rem;
            }
        }
    }

    .iconosGrid {
        @include m.grid(3, 1rem);
        justify-items: center;

        @include m.escritorio {
            @include m.grid(4, 2rem);
        }

        .iconosHabilidades {
            box-shadow: 1rem 1rem 1rem 0 rgba(0, 0, 0, 0.25);
            height: 8rem;
            width: 8rem;
            background-color: v.$amarrillo;
            border-radius: 50%;
            display: flex;
            justify-content: center;
            align-items: center;

            /* Logo de Visual Studio */
            &:nth-child(10) {
                grid-column: 2 / 3;
            }

            @include m.escritorio {
                height: 10rem;
                width: 10rem;

                &:nth-child(9) {
                    grid-column: 2 / 3;
                }

                &:nth-child(10) {
                    grid-column: 3 / 4;
                }
            }

            img {
                width: 4rem;

                @include m.escritorio {
                    width: 5rem;
                }
            }
        }
    }
}

.marginTop {
    margin-top: 10rem;
}

.segundaSeccion {
    text-align: center;

    @include m.tablet {
        @include m.grid(3, 3rem);
        justify-items: center;
    }

    p {
        font-size: 1.6rem;
        margin-bottom: 2rem;

        @include m.tablet {
            font-size: 2rem;
        }
    }

    img {
        width: 8rem;

        @include m.tablet {
            width: 10rem;
        }
    }
}

.terceraSeccion {
    display: flex;
    flex-direction: column;
    align-items: center;

    @include m.tablet {
        @include m.grid(2, 3rem);
        align-items: center;
    }

    @include m.escritorio {
        @include m.grid(3, 3rem);
    }

    figure {
        margin-bottom: 5rem;
        position: relative;
        width: 25rem;
        height: 15rem;
        overflow: hidden;
        border-radius: 0.5rem;
        box-shadow: 1rem 1rem 1rem 0 rgba(0, 0, 0, 0.25);

        @include m.tablet {
            width: 35rem;
            height: 25rem;
        }

        img {
            width: 100%;
            height: 100%;
            transition: all 400ms ease-out;
            will-change: transform;
        }

        .info {
            position: absolute;
            top: 0;
            width: 100%;
            height: 100%;
            background-color: rgb(0 0 0 / 50%);
            transition: all 400ms ease-out;
            opacity: 0;
            visibility: hidden;
            text-align: center;

            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;

            p {
                color: v.$blanco;
                font-size: 1.6rem;
            }

            a img {
                width: 5rem;
            }
        }

        &:hover .info {
            opacity: 1;
            visibility: visible;
        }

        &:hover .proyecto {
            transform: scale(1.3);
        }
    }

}

.cuartaSeccion {
    text-align: center;

    @include m.tablet {
        text-align: justify;
        @include m.grid(2, 3rem);
        align-items: center;
    }

    h4 {
        font-size: 2.5rem;

        @include m.escritorio {
            font-size: 3rem;
        }
    }

    p {
        font-size: 1.6rem;
        margin-bottom: 2rem;

        @include m.tablet {
            font-size: 1.6rem;
        }
    }

    input:not([type="submit"]) {
        border: none;
        background-color: v.$input;
        font-size: 2rem;
        height: 3rem;
        padding: 2rem 1rem;
        margin-bottom: 1.5rem;
        border-radius: 0.5rem;
        width: 100%;
    }

    input[type="submit"] {
        background-color: v.$azul;
        border: none;
    }

    input::placeholder,
    textarea::placeholder {
        color: v.$negro;
    }

    textarea {
        border: none;
        background-color: v.$input;
        font-size: 2rem;
        height: 10rem;
        padding: 0.5rem 1rem;
        resize: none;
        border-radius: 0.5rem;
        width: 100%;
    }

    a {
        background-color: v.$azul;
        border: none;
    }
}

.final {
    margin-top: 15rem;

    @include m.tablet {
        margin-top: 30rem;
    }
}

footer {
    color: v.$blanco;
    background-color: rgb(0 0 0 / 60%);
    height: 30rem;

    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    p {
        text-align: center;
        font-size: 1.6rem;
        margin-bottom: 2rem;
    }

    .iconosFlex {
        display: flex;
        gap: 3rem;

        .iconosFooter {
            height: 6rem;
            width: 6rem;
            background-color: v.$blanco;
            border-radius: 50%;
            display: flex;
            justify-content: center;
            align-items: center;

            img {
                width: 3rem;
            }
        }
    }

}

.galeria {
    box-shadow: 1rem 1rem 1rem 0 rgba(0, 0, 0, 0.25);

    img {
        transition: all 400ms ease-out;
        will-change: transform;

        &:hover {
            transform: scale(1.1);
        }
    }
}