@use 'base/variables' as v;
@use 'base/mixins' as m;

header {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 1;
}

.sombra {
    background-color: rgb(0 0 0 / 60%);
}

.subir {
    position: fixed;
    bottom: 0;
    right: 0;
    margin-bottom: 3rem;
    margin-right: 3rem;
    
    height: 4rem;
    width: 4rem;
    border: solid;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1;

    img {
        width: 2rem;
    }
    @include m.tablet {
        display: none;
    }
}

.header {
    padding: 1rem;

    @include m.grid(2, 0);
    align-items: center;

    @include m.tablet {
        padding: 1.5rem;
        display: flex;
        justify-content: space-between;
        align-items: center;
    }

    @include m.escritorio {
        padding: 2rem;
    }

    h1 {
        color: v.$blanco;
        text-align: left;
        font-weight: 700;

        @include m.tablet {
            letter-spacing: 0.5rem;
        }
    }

    /* Solamente los dispositivos pequeños podran ver el btn del menu */
    .mobile-menu {
        grid-column: 2 / 3;
        text-align: right;

        @include m.tablet {
            display: none;
        }

        /* Por defecto el svg viene en 16x16, es por eso que necesito agrandarlo */
        img {
            width: 3rem;

            @include m.tablet {
                width: 5rem;
            }
        }
    }

    .navegacion {
        opacity: 0;
        visibility: hidden;
        height: 0;
        // transition: visibility 0.3s ease-in-out, opacity 0.3s linear, height 0.5s linear;

        @include m.tablet {
            display: flex;
            gap: 2rem;
            align-items: center;
            opacity: 1;
            visibility: visible;
        }

        a {
            color: v.$gris;
            display: block;
            text-align: center;

            @include m.tablet {
                font-size: 1.5rem;
                margin-bottom: 0;
            }

            @include m.escritorio {
                font-size: 2rem;
                margin-bottom: 0;
            }

            &:hover {
                color: v.$input;
                text-decoration: underline;
            }
        }
    }

    .mostrar {
        visibility: visible;
        opacity: 1;
        height: auto;
    }
}

.seleccion {
    color: v.$blanco  !important;
}