@use 'variables' as v;
@use 'base/mixins' as m;

html {
    /* 1rem = 10px */
    font-size: 62.5%;
    box-sizing: border-box;
}

*,
*:before,
*:after {
    box-sizing: inherit;
}

body {
    font-family: v.$fuenteParrafos;
}

h2 {
    text-align: center;
    font-size: 2.5rem;
    font-weight: 700;
    font-family: v.$fuenteTitulos;
    @include m.tablet {
        text-align: justify;
        font-size: 3rem;
    }
    @include m.escritorio {
        font-size: 3.5rem;
    }
}

h3 {
    font-size: 1.6rem;
    color: v.$blanco;
    font-weight: 700;
    font-family: v.$fuenteTitulos;

    @include m.tablet {
        font-size: 2rem;
    }  
}

a {
    text-decoration: none;
}