@charset "UTF-8";
/* Colores */
/* Fuentes */
/* Tamaño de Media Queries */
html {
  /* 1rem = 10px */
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-family: "Open Sans", sans-serif;
}

h2 {
  text-align: center;
  font-size: 2.5rem;
  font-weight: 700;
  font-family: "PT Sans", sans-serif;
}
@media (min-width: 768px) {
  h2 {
    text-align: justify;
    font-size: 3rem;
  }
}
@media (min-width: 1024px) {
  h2 {
    font-size: 3.5rem;
  }
}

h3 {
  font-size: 1.6rem;
  color: #ffffff;
  font-weight: 700;
  font-family: "PT Sans", sans-serif;
}
@media (min-width: 768px) {
  h3 {
    font-size: 2rem;
  }
}

a {
  text-decoration: none;
}

header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 1;
}

.sombra {
  background-color: rgba(0, 0, 0, 0.6);
}

.subir {
  position: fixed;
  bottom: 0;
  right: 0;
  margin-bottom: 3rem;
  margin-right: 3rem;
  height: 4rem;
  width: 4rem;
  border: solid;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1;
}
.subir img {
  width: 2rem;
}
@media (min-width: 768px) {
  .subir {
    display: none;
  }
}

.header {
  padding: 1rem;
  display: grid;
  gap: 0;
  grid-template-columns: repeat(2, 1fr);
  align-items: center;
  /* Solamente los dispositivos pequeños podran ver el btn del menu */
}
@media (min-width: 768px) {
  .header {
    padding: 1.5rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}
@media (min-width: 1024px) {
  .header {
    padding: 2rem;
  }
}
.header h1 {
  color: #ffffff;
  text-align: left;
  font-weight: 700;
}
@media (min-width: 768px) {
  .header h1 {
    letter-spacing: 0.5rem;
  }
}
.header .mobile-menu {
  grid-column: 2/3;
  text-align: right;
  /* Por defecto el svg viene en 16x16, es por eso que necesito agrandarlo */
}
@media (min-width: 768px) {
  .header .mobile-menu {
    display: none;
  }
}
.header .mobile-menu img {
  width: 3rem;
}
@media (min-width: 768px) {
  .header .mobile-menu img {
    width: 5rem;
  }
}
.header .navegacion {
  opacity: 0;
  visibility: hidden;
  height: 0;
}
@media (min-width: 768px) {
  .header .navegacion {
    display: flex;
    gap: 2rem;
    align-items: center;
    opacity: 1;
    visibility: visible;
  }
}
.header .navegacion a {
  color: #cfc9c2;
  display: block;
  text-align: center;
}
@media (min-width: 768px) {
  .header .navegacion a {
    font-size: 1.5rem;
    margin-bottom: 0;
  }
}
@media (min-width: 1024px) {
  .header .navegacion a {
    font-size: 2rem;
    margin-bottom: 0;
  }
}
.header .navegacion a:hover {
  color: lightgray;
  text-decoration: underline;
}
.header .mostrar {
  visibility: visible;
  opacity: 1;
  height: auto;
}

.seleccion {
  color: #ffffff !important;
}

.hero {
  background-image: linear-gradient(rgba(0, 0, 0, 0.6), rgba(0, 0, 0, 0.6)), image-set(url("/build/img/code.png") 1x, url("/build/img/code.webp") 1x);
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;
  min-height: 100vh;
}
.hero .contenido-hero {
  color: #ffffff;
  position: absolute;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
@media (min-width: 768px) {
  .hero .contenido-hero {
    flex-direction: row;
    gap: 5rem;
  }
}
.hero .contenido-hero img {
  width: 20rem;
  height: 28rem;
}
@media (min-width: 768px) {
  .hero .contenido-hero img {
    width: 25rem;
    height: 35rem;
  }
}
@media (min-width: 1024px) {
  .hero .contenido-hero img {
    width: 35rem;
    height: 45rem;
  }
}
.hero .contenido-hero h1 {
  font-size: 3rem;
  font-weight: 700;
  font-family: "PT Sans", sans-serif;
  text-align: center;
  color: #17618b;
  letter-spacing: 1rem;
  padding: 1rem;
  margin-top: 1rem;
}
@media (min-width: 768px) {
  .hero .contenido-hero h1 {
    font-size: 3.5rem;
  }
}
@media (min-width: 1024px) {
  .hero .contenido-hero h1 {
    font-size: 4rem;
  }
}
.hero .contenido-hero p {
  text-align: center;
  color: #ffffff;
  font-size: 2rem;
}
@media (min-width: 768px) {
  .hero .contenido-hero p {
    font-size: 2rem;
  }
}
@media (min-width: 1024px) {
  .hero .contenido-hero p {
    font-size: 2.8rem;
  }
}
.hero .contenido-hero a {
  background-color: #17618b;
  border: none;
}

.primeraSeccion {
  text-align: center;
}
@media (min-width: 768px) {
  .primeraSeccion {
    display: grid;
    gap: 3rem;
    grid-template-columns: repeat(2, 1fr);
    text-align: justify;
  }
}
.primeraSeccion .sobreMi a {
  background-color: #17618b;
  border: none;
}
.primeraSeccion .sobreMi p {
  font-size: 2rem;
  line-height: 1.5;
}
@media (min-width: 768px) {
  .primeraSeccion .sobreMi p {
    font-size: 2.5rem;
  }
}
.primeraSeccion .iconosGrid {
  display: grid;
  gap: 1rem;
  grid-template-columns: repeat(3, 1fr);
  justify-items: center;
}
@media (min-width: 1024px) {
  .primeraSeccion .iconosGrid {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(4, 1fr);
  }
}
.primeraSeccion .iconosGrid .iconosHabilidades {
  box-shadow: 1rem 1rem 1rem 0 rgba(0, 0, 0, 0.25);
  height: 8rem;
  width: 8rem;
  background-color: gold;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  /* Logo de Visual Studio */
}
.primeraSeccion .iconosGrid .iconosHabilidades:nth-child(10) {
  grid-column: 2/3;
}
@media (min-width: 1024px) {
  .primeraSeccion .iconosGrid .iconosHabilidades {
    height: 10rem;
    width: 10rem;
  }
  .primeraSeccion .iconosGrid .iconosHabilidades:nth-child(9) {
    grid-column: 2/3;
  }
  .primeraSeccion .iconosGrid .iconosHabilidades:nth-child(10) {
    grid-column: 3/4;
  }
}
.primeraSeccion .iconosGrid .iconosHabilidades img {
  width: 4rem;
}
@media (min-width: 1024px) {
  .primeraSeccion .iconosGrid .iconosHabilidades img {
    width: 5rem;
  }
}

.marginTop {
  margin-top: 10rem;
}

.segundaSeccion {
  text-align: center;
}
@media (min-width: 768px) {
  .segundaSeccion {
    display: grid;
    gap: 3rem;
    grid-template-columns: repeat(3, 1fr);
    justify-items: center;
  }
}
.segundaSeccion p {
  font-size: 1.6rem;
  margin-bottom: 2rem;
}
@media (min-width: 768px) {
  .segundaSeccion p {
    font-size: 2rem;
  }
}
.segundaSeccion img {
  width: 8rem;
}
@media (min-width: 768px) {
  .segundaSeccion img {
    width: 10rem;
  }
}

.terceraSeccion {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .terceraSeccion {
    display: grid;
    gap: 3rem;
    grid-template-columns: repeat(2, 1fr);
    align-items: center;
  }
}
@media (min-width: 1024px) {
  .terceraSeccion {
    display: grid;
    gap: 3rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
.terceraSeccion figure {
  margin-bottom: 5rem;
  position: relative;
  width: 25rem;
  height: 15rem;
  overflow: hidden;
  border-radius: 0.5rem;
  box-shadow: 1rem 1rem 1rem 0 rgba(0, 0, 0, 0.25);
}
@media (min-width: 768px) {
  .terceraSeccion figure {
    width: 35rem;
    height: 25rem;
  }
}
.terceraSeccion figure img {
  width: 100%;
  height: 100%;
  transition: all 400ms ease-out;
  will-change: transform;
}
.terceraSeccion figure .info {
  position: absolute;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  transition: all 400ms ease-out;
  opacity: 0;
  visibility: hidden;
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.terceraSeccion figure .info p {
  color: #ffffff;
  font-size: 1.6rem;
}
.terceraSeccion figure .info a img {
  width: 5rem;
}
.terceraSeccion figure:hover .info {
  opacity: 1;
  visibility: visible;
}
.terceraSeccion figure:hover .proyecto {
  transform: scale(1.3);
}

.cuartaSeccion {
  text-align: center;
}
@media (min-width: 768px) {
  .cuartaSeccion {
    text-align: justify;
    display: grid;
    gap: 3rem;
    grid-template-columns: repeat(2, 1fr);
    align-items: center;
  }
}
.cuartaSeccion h4 {
  font-size: 2.5rem;
}
@media (min-width: 1024px) {
  .cuartaSeccion h4 {
    font-size: 3rem;
  }
}
.cuartaSeccion p {
  font-size: 1.6rem;
  margin-bottom: 2rem;
}
@media (min-width: 768px) {
  .cuartaSeccion p {
    font-size: 1.6rem;
  }
}
.cuartaSeccion input:not([type=submit]) {
  border: none;
  background-color: lightgray;
  font-size: 2rem;
  height: 3rem;
  padding: 2rem 1rem;
  margin-bottom: 1.5rem;
  border-radius: 0.5rem;
  width: 100%;
}
.cuartaSeccion input[type=submit] {
  background-color: #17618b;
  border: none;
}
.cuartaSeccion input::placeholder,
.cuartaSeccion textarea::placeholder {
  color: #2e393f;
}
.cuartaSeccion textarea {
  border: none;
  background-color: lightgray;
  font-size: 2rem;
  height: 10rem;
  padding: 0.5rem 1rem;
  resize: none;
  border-radius: 0.5rem;
  width: 100%;
}
.cuartaSeccion a {
  background-color: #17618b;
  border: none;
}

.final {
  margin-top: 15rem;
}
@media (min-width: 768px) {
  .final {
    margin-top: 30rem;
  }
}

footer {
  color: #ffffff;
  background-color: rgba(0, 0, 0, 0.6);
  height: 30rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
footer p {
  text-align: center;
  font-size: 1.6rem;
  margin-bottom: 2rem;
}
footer .iconosFlex {
  display: flex;
  gap: 3rem;
}
footer .iconosFlex .iconosFooter {
  height: 6rem;
  width: 6rem;
  background-color: #ffffff;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
}
footer .iconosFlex .iconosFooter img {
  width: 3rem;
}

.galeria {
  box-shadow: 1rem 1rem 1rem 0 rgba(0, 0, 0, 0.25);
}
.galeria img {
  transition: all 400ms ease-out;
  will-change: transform;
}
.galeria img:hover {
  transform: scale(1.1);
}